import FavoriteRestaurantIdb from '../../data/favorite-restaurants-idb';
import CONFIG from '../../globals/config';

const createRestaurantItemTemplate = (restaurant) => `
    <div class='restaurantInfo'>
    <img tabindex="0" class="pictureRestaurant" src= "${CONFIG.BASE_IMAGE_URL}${restaurant.picture}" alt="gambar restaurant">
    <h3 tabindex="0" class="cityRestaurant">Kota. ${restaurant.city}</h3>
    <div class="content">
    
    <h5 tabindex="0" class="nameRestaurant"><a href="/#/detail/${restaurant.id}">${restaurant.name}</a></h5>
    <p tabindex="0" class="descriptionRestaurant">${restaurant.description} </p>
    </div>
`;

const Favorite = {
  async render() {
    return `
        <h2 tabindex="0" class="headerFavoriteRestaurants">Your Favorite Restaurants</h2>
        <div id="restaurants" class="restaurants"></div>
        `;
  },

  async afterRender() {
    const restaurants = await FavoriteRestaurantIdb.getAllRestaurants();
    const restaurantsContainer = document.querySelector('#restaurants');

    restaurants.forEach((restaurant) => {
      restaurantsContainer.innerHTML += createRestaurantItemTemplate(restaurant);
    });
  },
};

export default Favorite;
